header {
  max-width: 700px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  background: none;
}

.scoreBox {
  display: flex;
  flex-flow: column nowrap;
  width: 125px;
  height: 100px;
  padding: 0.5em 0;
  background: #fff;
  border-radius: 10px;
  align-items: center;
}

.scoreBox h4 {
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #2a46c0;
  margin-top: 5px;
}

.scoreBox h2 {
  font-size: 3.5rem;
  margin-top: -5px;
  color: rgb(59, 67, 99);
}
