.selectionButtons {
  display: flex;
  margin: 0 auto;
  padding: 0px calc(5rem - 10vw);
  max-width: 900px;
  flex-flow: row nowrap;
  position: relative;
  justify-content: center;
  align-items: flex-start;
  height: 300px;
  transform: scale(1.2);
}

.optionButtons {
  position: relative;
  border: none;
  border-radius: 50%;
  transform: scale(1.4);
  width: 100px;
  height: 100px;
  cursor: pointer;
  z-index: 1;
}
.selectionButtons h3 {
  margin-bottom: 100px;
  color: #fff;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 100;
}
.revealSelectionWrapper {
  display: flex;
  flex-flow: column wrap;
}
.revealSelectionWrapper:nth-child(1) {
  padding-left: 40px;
}
.optionButtons::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0px 10px 1px 1px rgba(0, 0, 0, 0.1) inset;
}

#rockBTN {
  align-self: flex-end;
}

#rockBTN::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  width: 130px;
  height: 130px;
  z-index: -1;
  background: linear-gradient(#dc2e4e, #dd405d);
  box-shadow: 0px 5px 1px 1px rgba(248, 14, 57, 0.7);
  border-radius: 50%;
}

#paperBTN::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  width: 130px;
  height: 130px;
  z-index: -1;

  background: linear-gradient(#4865f4, #5671f5);
  border-radius: 50%;
  box-shadow: 0px 5px 0px 1px rgba(31, 68, 255, 0.9);
}
#scissorsBTN::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  width: 130px;
  height: 130px;
  z-index: -1;

  background: linear-gradient(#ec9e0e, #eca922);
  border-radius: 50%;
  box-shadow: -0.5px 5px 0px 0.2px rgba(204, 141, 23, 0.9);
}

span.selectionBase {
  z-index: -1;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

span.queueHouse {
  top: 115px;
  margin-left: 35px;
  width: 130px;
  height: 130px;
  position: absolute;
  z-index: 10;
  transform: scale(1.8);
  border-radius: 50%;
}

.selectionsAnimation-enter {
  position: absolute;
  opacity: 0;
  transform: translate(-100vw) scale(0.8);
}
.selectionsAnimation-enter-active {
  transform: translate(100vw) scale(1);
  opacity: 0;
}
.selectionsAnimation-enter-done {
  position: relative;
  opacity: 1;
  transition: opacity 1000ms, transform 500ms;
}

.selectionsAnimation-exit {
  opacity: 1;
}
.GameBoardResult.selectionsAnimation-exit-active {
  transition: opacity 500ms, transform 500ms;
  transform: translate(-100vw);
  opacity: 0;
}

.selectionsAnimation-exit-active {
  transition: opacity 500ms, transform 500ms;
  transform: rotateZ(180deg) scale(0.5);
  opacity: 0;
}
