@import url(https://fonts.googleapis.com/css?family=Barlow+Semi+Condensed:600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.rulesButton {
  position: absolute;
  bottom: 30px;
  right: 30px;
  padding: 1em 3.5em;
  background: none;
  border: 2px solid #e6e9ee;
  border-radius: 10px;
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  letter-spacing: 2px;
  cursor: pointer;
  -webkit-transition: 0.2s linear all;
  transition: 0.2s linear all;
  z-index: 100;
}

.rulesButton:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.rulesModal {
  text-align: center;
  position: absolute;
  width: 400px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  height: auto;
  padding: 2rem;
  background: #fff;
  border-radius: 10px;
  z-index: 100;
}
#modal-portal {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
.rulesModal h2 {
  text-align: left;
  display: inline;
  text-transform: uppercase;
  color: #3b3737;
  font-weight: 800;
  font-size: 2rem;
  letter-spacing: 1px;
}
.rulesModal img {
  padding-top: 2em;
}
.rulesModal span img {
  float: right;
  padding: 0;
}
#modal-close {
  cursor: pointer;
}

.results {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  margin-right: -30px;
  min-height: 300px;
}
.results h1 {
  color: #fff;
  font-size: 3.5rem;
  font-weight: 800;
  letter-spacing: 2px;
  padding-bottom: 20px;
  text-transform: uppercase;
}
#playAgain {
  padding: 15px 60px;
  border: none;
  border-radius: 10px;
  background: #e3e8f0;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: rgb(199, 51, 75);
}
.results.selectionsAnimation-enter {
  -webkit-transform: none;
          transform: none;
  opacity: 0;
  padding: 0;
}
.results.selectionsAnimation-enter-active {
  opacity: 0;
}

.results.selectionsAnimation-exit {
  display: none;
}

.selectionButtons {
  display: flex;
  margin: 0 auto;
  padding: 0px calc(5rem - 10vw);
  max-width: 900px;
  flex-flow: row nowrap;
  position: relative;
  justify-content: center;
  align-items: flex-start;
  height: 300px;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.optionButtons {
  position: relative;
  border: none;
  border-radius: 50%;
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  width: 100px;
  height: 100px;
  cursor: pointer;
  z-index: 1;
}
.selectionButtons h3 {
  margin-bottom: 100px;
  color: #fff;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 100;
}
.revealSelectionWrapper {
  display: flex;
  flex-flow: column wrap;
}
.revealSelectionWrapper:nth-child(1) {
  padding-left: 40px;
}
.optionButtons::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0px 10px 1px 1px rgba(0, 0, 0, 0.1) inset;
}

#rockBTN {
  align-self: flex-end;
}

#rockBTN::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  width: 130px;
  height: 130px;
  z-index: -1;
  background: -webkit-gradient(linear, left top, left bottom, from(#dc2e4e), to(#dd405d));
  background: linear-gradient(#dc2e4e, #dd405d);
  box-shadow: 0px 5px 1px 1px rgba(248, 14, 57, 0.7);
  border-radius: 50%;
}

#paperBTN::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  width: 130px;
  height: 130px;
  z-index: -1;

  background: -webkit-gradient(linear, left top, left bottom, from(#4865f4), to(#5671f5));

  background: linear-gradient(#4865f4, #5671f5);
  border-radius: 50%;
  box-shadow: 0px 5px 0px 1px rgba(31, 68, 255, 0.9);
}
#scissorsBTN::before {
  content: '';
  position: absolute;
  top: -15px;
  left: -15px;
  width: 130px;
  height: 130px;
  z-index: -1;

  background: -webkit-gradient(linear, left top, left bottom, from(#ec9e0e), to(#eca922));

  background: linear-gradient(#ec9e0e, #eca922);
  border-radius: 50%;
  box-shadow: -0.5px 5px 0px 0.2px rgba(204, 141, 23, 0.9);
}

span.selectionBase {
  z-index: -1;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

span.queueHouse {
  top: 115px;
  margin-left: 35px;
  width: 130px;
  height: 130px;
  position: absolute;
  z-index: 10;
  -webkit-transform: scale(1.8);
          transform: scale(1.8);
  border-radius: 50%;
}

.selectionsAnimation-enter {
  position: absolute;
  opacity: 0;
  -webkit-transform: translate(-100vw) scale(0.8);
          transform: translate(-100vw) scale(0.8);
}
.selectionsAnimation-enter-active {
  -webkit-transform: translate(100vw) scale(1);
          transform: translate(100vw) scale(1);
  opacity: 0;
}
.selectionsAnimation-enter-done {
  position: relative;
  opacity: 1;
  -webkit-transition: opacity 1000ms, -webkit-transform 500ms;
  transition: opacity 1000ms, -webkit-transform 500ms;
  transition: opacity 1000ms, transform 500ms;
  transition: opacity 1000ms, transform 500ms, -webkit-transform 500ms;
}

.selectionsAnimation-exit {
  opacity: 1;
}
.GameBoardResult.selectionsAnimation-exit-active {
  -webkit-transition: opacity 500ms, -webkit-transform 500ms;
  transition: opacity 500ms, -webkit-transform 500ms;
  transition: opacity 500ms, transform 500ms;
  transition: opacity 500ms, transform 500ms, -webkit-transform 500ms;
  -webkit-transform: translate(-100vw);
          transform: translate(-100vw);
  opacity: 0;
}

.selectionsAnimation-exit-active {
  -webkit-transition: opacity 500ms, -webkit-transform 500ms;
  transition: opacity 500ms, -webkit-transform 500ms;
  transition: opacity 500ms, transform 500ms;
  transition: opacity 500ms, transform 500ms, -webkit-transform 500ms;
  -webkit-transform: rotateZ(180deg) scale(0.5);
          transform: rotateZ(180deg) scale(0.5);
  opacity: 0;
}

section.GameBoard {
  padding: calc(40vh - 20vh) 1rem;
}

header {
  max-width: 700px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 1em;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  background: none;
}

.scoreBox {
  display: flex;
  flex-flow: column nowrap;
  width: 125px;
  height: 100px;
  padding: 0.5em 0;
  background: #fff;
  border-radius: 10px;
  align-items: center;
}

.scoreBox h4 {
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #2a46c0;
  margin-top: 5px;
}

.scoreBox h2 {
  font-size: 3.5rem;
  margin-top: -5px;
  color: rgb(59, 67, 99);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow Semi Condensed', sans-serif;
}

body {
  overflow: hidden;
  min-height: 100vh;
  background: radial-gradient(circle at center, #1f3756, #141539);
}
.App {
  padding: 1em;
}

footer {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 2em 0;
  color: #fff;
}

footer a {
  text-decoration: none;
  color: #7e8fa7;
  font-style: italic;
}

