@import url('https://fonts.googleapis.com/css?family=Barlow+Semi+Condensed:600,700&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow Semi Condensed', sans-serif;
}

body {
  overflow: hidden;
  min-height: 100vh;
  background: radial-gradient(circle at center, #1f3756, #141539);
}
.App {
  padding: 1em;
}

footer {
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 2em 0;
  color: #fff;
}

footer a {
  text-decoration: none;
  color: #7e8fa7;
  font-style: italic;
}
