.rulesButton {
  position: absolute;
  bottom: 30px;
  right: 30px;
  padding: 1em 3.5em;
  background: none;
  border: 2px solid #e6e9ee;
  border-radius: 10px;
  text-transform: uppercase;
  color: #fff;
  font-size: 16px;
  letter-spacing: 2px;
  cursor: pointer;
  transition: 0.2s linear all;
  z-index: 100;
}

.rulesButton:hover {
  transform: scale(1.02);
}

.rulesModal {
  text-align: center;
  position: absolute;
  width: 400px;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  height: auto;
  padding: 2rem;
  background: #fff;
  border-radius: 10px;
  z-index: 100;
}
#modal-portal {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
.rulesModal h2 {
  text-align: left;
  display: inline;
  text-transform: uppercase;
  color: #3b3737;
  font-weight: 800;
  font-size: 2rem;
  letter-spacing: 1px;
}
.rulesModal img {
  padding-top: 2em;
}
.rulesModal span img {
  float: right;
  padding: 0;
}
#modal-close {
  cursor: pointer;
}
