.results {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  margin-right: -30px;
  min-height: 300px;
}
.results h1 {
  color: #fff;
  font-size: 3.5rem;
  font-weight: 800;
  letter-spacing: 2px;
  padding-bottom: 20px;
  text-transform: uppercase;
}
#playAgain {
  padding: 15px 60px;
  border: none;
  border-radius: 10px;
  background: #e3e8f0;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: rgb(199, 51, 75);
}
.results.selectionsAnimation-enter {
  transform: none;
  opacity: 0;
  padding: 0;
}
.results.selectionsAnimation-enter-active {
  opacity: 0;
}

.results.selectionsAnimation-exit {
  display: none;
}
